.front--div{
    width: 100%;
    padding: 1rem;
    height: calc(100vh - 3rem);
}
.text{

    .titulo{
        h1{
            text-align: start;
            font-size: 5rem;
        }
        span{
            color: $naranja--span;
        }
    }
    h3{
        font-weight: 400;
        font-size: 2.5rem;
        text-align: start;
    }
    p{
        font-weight: 300;
        max-width: 65rem;
    }
}

.front--div--btn{
    background-color: $naranja--claro;
    border: none;
    padding: 1rem;
    border-radius: .5rem;
    color: $fuente;
    margin-bottom: 2rem;
    cursor: pointer;
    &:hover{
        transform: scale(1.05);
        transition: ease-in all .2s;
        color: $gris;
    }
}
.iconos{
    color: $gris;
    display: flex;
    gap: 1rem;
    padding-bottom: 2rem;
    a{
        color: $gris;
    }
    i{
        cursor: pointer;
        &:hover{
            color: $naranja--claro;
        }
    }
}
.img--front--div{
    display: flex;
    justify-content: center;
    margin-top: 8rem;
}

@media (min-width: 65rem) {
    .front--div{
        display: flex;
        justify-content: center;
        width: 100%;
        gap: 7rem;
    }
    .text{
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .img--front--div{
        width: 30%;
        img{
            object-fit: contain;
        }
    }
}