.header{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    height: 3rem;
    .logo{
        height: 3rem;
        cursor: pointer;
        img{
            height: 100%;
        }
    }
    .more--menu{
        display: flex;
        justify-content: center;
        gap: 2rem;
        color: $gris;
        i{
            cursor: pointer;
            &:hover{
                color: $fuente;
            }
        }
    }
}

.menu--primario{
    position: absolute;
    background-color: $fondo--claro;
    top: 3rem;
    right: 3rem;
    .cv--gmail{
        margin: 0;
        padding: 0;
        display: none;
        i{
            cursor: pointer;
            &:hover{
                color: $naranja--claro;
            }
        }
        a{
            color: $gris;
        }
    }
    .cv, .gmail{
        display: flex;
        align-items: center;
        gap: 1rem;
    }
    .parrafo{
        color: $fuente;
        &:hover{
            color: $naranja--claro;
            cursor: pointer;
        }
    }
}

.show{
    padding: 2rem;
    transition: all .5s ease;
    .cv--gmail{
        display: block;
    }
}

.menu--secundario{
    position: absolute;
    top: 3rem;
    right: -60rem;
    background-color: $fondo--claro;
    padding: 5rem;
    margin: 0;
    text-align: end;
    height: 100%;
    .parrafos--menu{
        transition: all .1s ease-in;
        p{
            cursor: pointer;
            &:hover{
                color: $naranja--claro;
            }
        }
    }
}

.show-2{
    right: 0;
    transition: ease-in all .5s;
}

body{
    overflow-x: hidden;
}

@media (min-width: 76rem){
    .header{
        .more--menu{
            gap: 10rem;
            align-items: center;
        }    
    }
    .more--text{
        display: none;
    }
    .menu--primario{
        top: 0;
        position: static;
        .cv--gmail{
            display: flex;
            gap: 2rem;
            p{
                margin: 0;
            }
        }
    }
    
}