body{
    overflow-x: hidden;
}

.wrapper{
    .overlay{
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        z-index: 2;
    }
    .first{
        background-color: $fondo--oscuro;
    }
    .second{
        background-color: $fondo--oscuro;
        left: 33.3%;
    }
    .third{
        background-color: $fondo--oscuro;
        left: 66.6%;
    }
}