.skills{
    width: 100%;
    padding: 1rem;
    background-color: $fondo--oscuro;
    margin-bottom: 2rem;
}

.skills--img{
    width: 100%;
    display: flex;
    justify-content: center;
    img{
    object-fit: contain;    
}
}
.skills--text{
    h3{
        font-weight: 400;
    }
}

.skills--iconos{
    display: flex;
    gap: 1.5rem;
    width: 100%;
    justify-content: center;
    margin: 5rem 0;
    color: $naranja--span;
    flex-wrap: wrap;
}
@media (min-width: 60rem) {
    .skills--container{
        display: flex;
        width: 100%;
        justify-content: center;
        gap: 7rem;
        .skills--img--container{
            width: 30%;
        }
        .skills--text--container{
            width: 30%;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 3rem;
        }
    }
}