html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #353b41;
  color: #f8f9fd;
}

p {
  font-size: 2rem;
  color: #a7a8ac;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.5rem;
}

.span--subtitle {
  margin-bottom: 3rem;
}
.span--subtitle span {
  padding: 0.5rem;
  background-color: #50201b;
  font-size: 1.5rem;
  font-weight: 300;
  color: #df5646;
}

img {
  width: 100%;
  max-width: 55rem;
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  height: 3rem;
}
.header .logo {
  height: 3rem;
  cursor: pointer;
}
.header .logo img {
  height: 100%;
}
.header .more--menu {
  display: flex;
  justify-content: center;
  gap: 2rem;
  color: #a7a8ac;
}
.header .more--menu i {
  cursor: pointer;
}
.header .more--menu i:hover {
  color: #f8f9fd;
}

.menu--primario {
  position: absolute;
  background-color: #353b41;
  top: 3rem;
  right: 3rem;
}
.menu--primario .cv--gmail {
  margin: 0;
  padding: 0;
  display: none;
}
.menu--primario .cv--gmail i {
  cursor: pointer;
}
.menu--primario .cv--gmail i:hover {
  color: #df5646;
}
.menu--primario .cv--gmail a {
  color: #a7a8ac;
}
.menu--primario .cv, .menu--primario .gmail {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.menu--primario .parrafo {
  color: #f8f9fd;
}
.menu--primario .parrafo:hover {
  color: #df5646;
  cursor: pointer;
}

.show {
  padding: 2rem;
  transition: all 0.5s ease;
}
.show .cv--gmail {
  display: block;
}

.menu--secundario {
  position: absolute;
  top: 3rem;
  right: -60rem;
  background-color: #353b41;
  padding: 5rem;
  margin: 0;
  text-align: end;
  height: 100%;
}
.menu--secundario .parrafos--menu {
  transition: all 0.1s ease-in;
}
.menu--secundario .parrafos--menu p {
  cursor: pointer;
}
.menu--secundario .parrafos--menu p:hover {
  color: #df5646;
}

.show-2 {
  right: 0;
  transition: ease-in all 0.5s;
}

body {
  overflow-x: hidden;
}

@media (min-width: 76rem) {
  .header .more--menu {
    gap: 10rem;
    align-items: center;
  }

  .more--text {
    display: none;
  }

  .menu--primario {
    top: 0;
    position: static;
  }
  .menu--primario .cv--gmail {
    display: flex;
    gap: 2rem;
  }
  .menu--primario .cv--gmail p {
    margin: 0;
  }
}
.front--div {
  width: 100%;
  padding: 1rem;
  height: calc(100vh - 3rem);
}

.text .titulo h1 {
  text-align: start;
  font-size: 5rem;
}
.text .titulo span {
  color: #df5343;
}
.text h3 {
  font-weight: 400;
  font-size: 2.5rem;
  text-align: start;
}
.text p {
  font-weight: 300;
  max-width: 65rem;
}

.front--div--btn {
  background-color: #df5646;
  border: none;
  padding: 1rem;
  border-radius: 0.5rem;
  color: #f8f9fd;
  margin-bottom: 2rem;
  cursor: pointer;
}
.front--div--btn:hover {
  transform: scale(1.05);
  transition: ease-in all 0.2s;
  color: #a7a8ac;
}

.iconos {
  color: #a7a8ac;
  display: flex;
  gap: 1rem;
  padding-bottom: 2rem;
}
.iconos a {
  color: #a7a8ac;
}
.iconos i {
  cursor: pointer;
}
.iconos i:hover {
  color: #df5646;
}

.img--front--div {
  display: flex;
  justify-content: center;
  margin-top: 8rem;
}

@media (min-width: 65rem) {
  .front--div {
    display: flex;
    justify-content: center;
    width: 100%;
    gap: 7rem;
  }

  .text {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .img--front--div {
    width: 30%;
  }
  .img--front--div img {
    object-fit: contain;
  }
}
.skills {
  width: 100%;
  padding: 1rem;
  background-color: #2a2f34;
  margin-bottom: 2rem;
}

.skills--img {
  width: 100%;
  display: flex;
  justify-content: center;
}
.skills--img img {
  object-fit: contain;
}

.skills--text h3 {
  font-weight: 400;
}

.skills--iconos {
  display: flex;
  gap: 1.5rem;
  width: 100%;
  justify-content: center;
  margin: 5rem 0;
  color: #df5343;
  flex-wrap: wrap;
}

@media (min-width: 60rem) {
  .skills--container {
    display: flex;
    width: 100%;
    justify-content: center;
    gap: 7rem;
  }
  .skills--container .skills--img--container {
    width: 30%;
  }
  .skills--container .skills--text--container {
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
  }
}
.portafolio {
  padding: 1rem;
}

.proyectos--card {
  padding: 2rem;
  background: rgba(42, 47, 52, 0.8);
  backdrop-filter: blur(8.5px);
  -webkit-backdrop-filter: blur(8.5px);
  border-radius: 10px;
  cursor: pointer;
}
.proyectos--card:hover {
  transform: scale(1.1);
  transition: all ease 0.3s;
}
.proyectos--card img {
  border-radius: 10px;
}

.proyectos--cards {
  overflow: hidden;
}

.glider-track {
  gap: 1rem;
}

.glider-dot {
  background-color: #df5646;
}
.glider-dot:hover {
  background-color: #50201b;
}

.glider-dots .active {
  background-color: #50201b;
}

.contacto {
  padding: 1rem;
}

.contacto--iconos {
  color: #a7a8ac;
}
.contacto--iconos a {
  color: #a7a8ac;
}
.contacto--iconos .container {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.contacto--iconos .container i {
  margin-right: 2rem;
}
.contacto--iconos .container i:hover {
  color: #df5646;
}
.contacto--iconos .parrafo {
  margin: 0;
  cursor: pointer;
}
.contacto--iconos .parrafo:hover {
  color: #df5646;
  text-decoration: underline;
}

.svg {
  position: absolute;
  width: calc(100% - 1rem);
}

@media (min-width: 60rem) {
  .contacto--iconos {
    width: 100%;
    display: flex;
    gap: 5rem;
    justify-content: center;
    margin-bottom: 6rem;
  }
}
body {
  overflow-x: hidden;
}

.wrapper .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  z-index: 2;
}
.wrapper .first {
  background-color: #2a2f34;
}
.wrapper .second {
  background-color: #2a2f34;
  left: 33.3%;
}
.wrapper .third {
  background-color: #2a2f34;
  left: 66.6%;
}