.portafolio{
    padding: 1rem;
}

.proyectos--card{
    padding: 2rem;
    background: rgba( 42, 47, 52, 0.8 );
    backdrop-filter: blur( 8.5px );
    -webkit-backdrop-filter: blur( 8.5px );
    border-radius: 10px;
    &:hover{
        transform: scale(1.1);
        transition: all ease .3s;
    }
    img{
            border-radius: 10px;
    }
    cursor: pointer;

}
.proyectos--cards{
    overflow: hidden;
}
.glider-track{
    gap: 1rem;
}
.glider-dot{
    background-color: $naranja--claro;
    &:hover{
        background-color: $naranja--oscuro;
    }
}
.glider-dots{
    .active{
        background-color: $naranja--oscuro
    }
}