html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente--principal;
    font-size: 1.6rem;
    background-color: $fondo--claro;
    color: $fuente;
}
p {
    font-size: 2rem;
    color: $gris;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    font-family: $fuente--principal;
    font-weight: 900;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 2.5rem;
}
.span--subtitle{
    span{
        padding: .5rem;
        background-color: $naranja--oscuro;
        font-size: 1.5rem;
        font-weight: 300;
        color: $naranja--claro;
    }
    margin-bottom: 3rem;
}

img{
    width: 100%;
    max-width: 55rem;
}