.contacto{
    padding: 1rem;
}

.contacto--iconos{
    color: $gris;
    a{
        color: $gris;
    }

    .container{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        i{
            margin-right: 2rem;
            &:hover{
                color: $naranja--claro;
            }
        }
    }

    .parrafo{
        margin: 0;
        cursor: pointer;
        &:hover{
            color: $naranja--claro;
            text-decoration: underline;
        }
    }
}
.svg{
    position: absolute;
    width: calc(100% - 1rem);
}

@media (min-width: 60rem) {
    .contacto--iconos{
        width: 100%;
        display: flex;
        gap: 5rem;
        justify-content: center;
        margin-bottom: 6rem;
    }
}